
"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import type { NavItem } from "@/lib/constants";
import { APP_NAME } from "@/lib/constants"; // MAIN_NAV_LINKS import removed as it's not directly used here anymore
import { Layers } from "lucide-react"; // Updated to Layers

interface MainNavProps {
  items: NavItem[];
  className?: string;
}

export function MainNav({ items, className }: MainNavProps) {
  const pathname = usePathname();

  return (
    <div className="mr-4 hidden md:flex">
      <Link href="/" className="mr-6 flex items-center space-x-2">
        <Layers className="h-6 w-6 text-primary" /> {/* Updated Icon */}
        <span className="hidden font-bold sm:inline-block">
          {APP_NAME} {/* Will use updated APP_NAME from constants */}
        </span>
      </Link>
      <nav className={cn("flex items-center gap-4 text-sm lg:gap-6", className)}>
        {/* 
          The 'items' prop is already filtered in the Header component 
          to include only non-collapsible items.
          So, we can directly map over 'items' here.
        */}
        {items.map((item) => (
          <Link
            key={item.href}
            href={item.href}
            className={cn(
              "transition-colors hover:text-foreground/80",
              (item.match ? item.match(pathname) : pathname === item.href)
                ? "text-foreground"
                : "text-foreground/60"
            )}
          >
            {item.label}
          </Link>
        ))}
      </nav>
    </div>
  );
}
