
"use client";

import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { PlusCircle } from "lucide-react";
import { cn } from "@/lib/utils";
import { useCustomerActions } from "@/contexts/customer-actions-context"; // Import the context hook

export function SubHeader() {
  const pathname = usePathname();
  
  // Try to get context, will be undefined if not within provider
  let customerActions;
  try {
    customerActions = useCustomerActions();
  } catch (e) {
    // Context not available on this page, which is fine.
    customerActions = null;
  }
  const setIsNewCustomerSheetOpen = customerActions?.setIsNewCustomerSheetOpen;

  return (
    <div className="sticky top-14 z-40 w-full border-b border-border/40 bg-background/80 backdrop-blur supports-[backdrop-filter]:bg-background/50">
      <div className="container flex h-12 max-w-screen-2xl items-center justify-between px-4">
        {/* Left side of sub-header (e.g., for breadcrumbs or page titles) */}
        <div>
          {pathname === '/admin/customers' && (
            <span className="text-sm font-semibold text-foreground">Customer Management</span>
          )}
          {/* Add other page-specific titles here if needed, e.g.: */}
          {/* {pathname === '/admin/products' && (
            <span className="text-sm font-semibold text-foreground">Product Catalog</span>
          )} */}
        </div>

        {/* Right side of sub-header (e.g., for action buttons) */}
        <div>
          {pathname === '/admin/customers' && setIsNewCustomerSheetOpen && (
            <Button size="sm" onClick={() => setIsNewCustomerSheetOpen(true)}>
              <PlusCircle className="h-4 w-4" />
              <span className="hidden sm:ml-2 sm:inline">New Customer</span>
            </Button>
          )}
          {/* Add other page-specific buttons here if needed */}
        </div>
      </div>
    </div>
  );
}
