
"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import type { ReactNode } from 'react';
import React, { useState } from 'react';
import {
  SidebarProvider,
  Sidebar,
  SidebarHeader,
  SidebarFooter,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  SidebarMenuSub,
  SidebarMenuSubItem,
  SidebarMenuSubButton,
  SidebarInset,
  useSidebar,
} from "@/components/ui/sidebar";
import { Header } from "@/components/core/header";
import { SubHeader } from "@/components/core/sub-header";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { APP_NAME, MAIN_NAV_LINKS } from "@/lib/constants";
import type { NavItem } from "@/lib/constants";
import { Layers, ChevronDown, ChevronRight, MoreHorizontal } from "lucide-react"; 
import { cn } from "@/lib/utils";
import { useAuth } from "@/contexts/auth-context";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger, DropdownMenuSeparator, DropdownMenuLabel } from "@/components/ui/dropdown-menu";
import { Badge } from "@/components/ui/badge";
import { SheetTitle } from "@/components/ui/sheet"; 
import { CustomerActionsProvider } from '@/contexts/customer-actions-context';

function AppSidebar() {
  const pathname = usePathname();
  const { state, isMobile } = useSidebar(); 
  const { isAuthenticated, user, logout } = useAuth();

  const [openCollapsible, setOpenCollapsible] = useState<Record<string, boolean>>(
    MAIN_NAV_LINKS.filter(item => item.isCollapsible).reduce((acc, item) => {
      acc[item.label] = true; 
      return acc;
    }, {} as Record<string, boolean>)
  );

  const toggleCollapsible = (label: string) => {
    setOpenCollapsible(prev => ({ ...prev, [label]: !prev[label] }));
  };

  if (!isAuthenticated) {
    return null;
  }

  const renderNavItem = (item: NavItem, isSubItem = false) => {
    const isActive = item.match ? item.match(pathname) : pathname === item.href;
    const commonProps = {
      isActive,
      tooltip: state === "collapsed" ? item.label : undefined,
      className: "justify-start w-full", 
    };

    if (item.isCollapsible) {
      return (
        <SidebarMenuItem key={item.label}>
          <SidebarMenuButton
            {...commonProps}
            onClick={() => toggleCollapsible(item.label)}
            aria-expanded={openCollapsible[item.label]}
            className={cn(commonProps.className, "justify-between")}
          >
            <div className="flex items-center gap-2">
              <item.icon className="h-4 w-4" />
              <span>{item.label}</span>
            </div>
            <div className="flex items-center">
              {item.badge && state !== "collapsed" && (
                <Badge
                  variant="outline"
                  className={cn(
                    "h-5 min-w-[20px] px-1.5 text-xs flex items-center justify-center rounded-full mr-1",
                    item.badge.colorClasses
                  )}
                >
                  {item.badge.count}
                </Badge>
              )}
              {openCollapsible[item.label] ? <ChevronDown className="h-4 w-4" /> : <ChevronRight className="h-4 w-4" />}
            </div>
          </SidebarMenuButton>
          {openCollapsible[item.label] && item.subItems && item.subItems.length > 0 && (
            <SidebarMenuSub>
              {item.subItems.map(sub => renderNavItem(sub, true))}
            </SidebarMenuSub>
          )}
        </SidebarMenuItem>
      );
    }

    if (isSubItem) {
      return (
        <SidebarMenuSubItem key={item.href}>
          <Link href={item.href} passHref legacyBehavior>
            <SidebarMenuSubButton {...commonProps} isActive={isActive}>
              <span>{item.label}</span>
              {item.badge && state !== "collapsed" && (
                <Badge
                  variant="outline"
                  className={cn(
                    "ml-auto h-5 min-w-[20px] px-1.5 text-xs flex items-center justify-center rounded-full",
                    item.badge.colorClasses
                  )}
                >
                  {item.badge.count}
                </Badge>
              )}
            </SidebarMenuSubButton>
          </Link>
        </SidebarMenuSubItem>
      );
    }

    return (
      <SidebarMenuItem key={item.href}>
        <Link href={item.href} passHref legacyBehavior>
          <SidebarMenuButton {...commonProps}>
            <item.icon className="h-4 w-4" />
            <span>{item.label}</span>
            {item.badge && state !== "collapsed" && (
               <Badge
                variant="outline"
                className={cn(
                  "ml-auto h-5 min-w-[20px] px-1.5 text-xs flex items-center justify-center rounded-full",
                  item.badge.colorClasses
                )}
              >
                {item.badge.count}
              </Badge>
            )}
          </SidebarMenuButton>
        </Link>
      </SidebarMenuItem>
    );
  };
  
  return (
    <Sidebar collapsible="icon" variant="sidebar" side="left" className="border-r bg-sidebar text-sidebar-foreground">
      <SidebarHeader className="p-4">
        <Link href="/" className="flex items-center gap-2">
          <Layers className="h-6 w-6 text-sidebar-primary" />
          {isMobile ? (
            <SheetTitle className={cn("text-lg font-semibold")}>{APP_NAME}</SheetTitle>
          ) : (
            <h1 className={cn("text-lg font-semibold", state === "collapsed" && "hidden")}>{APP_NAME}</h1>
          )}
        </Link>
      </SidebarHeader>
      <ScrollArea className="flex-grow">
        <SidebarContent className="p-2">
          <SidebarMenu>
            {MAIN_NAV_LINKS.map(item => renderNavItem(item))}
          </SidebarMenu>
        </SidebarContent>
      </ScrollArea>
      <SidebarFooter className="p-2 border-t border-sidebar-border">
         <div className={cn("flex items-center gap-3 p-2", state === "collapsed" && "justify-center")}>
            <Avatar className="h-8 w-8">
              <AvatarImage src={user?.email ? `https://avatar.vercel.sh/${user.email}.png` : "https://placehold.co/40x40.png"} alt={user?.name || "User"} />
              <AvatarFallback>{user?.name ? user.name.substring(0, 2).toUpperCase() : "AU"}</AvatarFallback>
            </Avatar>
            <div className={cn("flex-grow", state === "collapsed" && "hidden")}>
              <p className="text-sm font-semibold text-sidebar-foreground">{user?.name || "Admin User"}</p>
              <p className="text-xs text-sidebar-foreground/70">{user?.email || "admin@swiftpanel.com"}</p>
            </div>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" size="icon" className={cn("h-8 w-8 text-sidebar-foreground/70 hover:text-sidebar-foreground hover:bg-sidebar-accent", state === "collapsed" && "hidden")}>
                  <MoreHorizontal className="h-4 w-4" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="bg-popover text-popover-foreground">
                <DropdownMenuLabel>My Account</DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <Link href="/settings">Settings</Link>
                </DropdownMenuItem>
                <DropdownMenuItem disabled>Support</DropdownMenuItem>
                <DropdownMenuSeparator />
                <DropdownMenuItem onClick={logout}>Logout</DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
      </SidebarFooter>
    </Sidebar>
  );
}


export default function AppLayout({ children }: { children: ReactNode }) {
  const { isLoading, isAuthenticated } = useAuth();

  if (isLoading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <div className="h-16 w-16 animate-spin rounded-full border-4 border-primary border-t-transparent"></div>
      </div>
    );
  }
  
  if (!isAuthenticated) {
    return null; 
  }

  return (
    <CustomerActionsProvider>
      <SidebarProvider defaultOpen={true}>
        <AppSidebar />
        <SidebarInset className="flex flex-col min-h-screen overflow-hidden">
          <Header />
          <SubHeader />
          <main className="flex-1 overflow-y-auto p-4 md:p-8 bg-background">
            {children}
          </main>
        </SidebarInset>
      </SidebarProvider>
    </CustomerActionsProvider>
  );
}

