
"use client";

import type { Dispatch, SetStateAction} from 'react';
import { createContext, useContext, useState } from 'react';

interface CustomerActionsContextType {
  isNewCustomerSheetOpen: boolean;
  setIsNewCustomerSheetOpen: Dispatch<SetStateAction<boolean>>;
}

const CustomerActionsContext = createContext<CustomerActionsContextType | undefined>(undefined);

export function CustomerActionsProvider({ children }: { children: React.ReactNode }) {
  const [isNewCustomerSheetOpen, setIsNewCustomerSheetOpen] = useState(false);

  return (
    <CustomerActionsContext.Provider value={{ isNewCustomerSheetOpen, setIsNewCustomerSheetOpen }}>
      {children}
    </CustomerActionsContext.Provider>
  );
}

export function useCustomerActions() {
  const context = useContext(CustomerActionsContext);
  if (context === undefined) {
    throw new Error("useCustomerActions must be used within a CustomerActionsProvider");
  }
  return context;
}
